@import url(https://fonts.googleapis.com/css?family=Ubuntu&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Uncomment to use only in development mode */
/* * {
    background: rgba(255, 0, 0, .1);
    box-shadow: 0 0 0 1px red;
} */

body {
    align-items: center;
    background-color: #fff;
    display: flex;
    height: 100%;
    justify-content: center;
    outline: none;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}

div:focus {
    outline-color: #3e7327;
    outline-style: dashed;
}

/* This 'containerEunit' class literally does nothing; but leave it there! */
.containerEunit {
    background-color: inherit;
}

.App {}

.botIcon {
    position: fixed;
    left: -200px;
    top: 180px;
    width: 400px;
}

.botOutputsContainer {
    position: fixed;
    top: 25%;
    left: 25%;
    right: 5%;
    padding: 0;
    margin: 0;
    width: 100%;

}

.chatbotOutput {
    background-color: rgba(255, 255, 255, 0.7);
    box-shadow: 2px 2px 3px 0 rgba(0, 0, 0, 0.25);
    text-align: left;
    font-family: Ubuntu, sans-serif;
    border: 0.1pt solid lightgray;
    position: relative;
    justify-content: left;
    font-size: 20px;
}

.chatbotOutput {
    min-width: 600px;
    width: 38%;
    max-width: 800px;
    line-height: 36px;
    border-radius: 10px;
    padding: 45px;
    display: inline-block;
    margin: 0 14px 14px 0;
    /*background-color: rgba(70, 133, 70, 0.7);
      color:white;*/
}

.quickReplyContainer {
    word-wrap: normal;
    white-space: nowrap;
}

.quickReplyContainer {
    display: flex;
    margin: 0 auto;
    position: absolute;
    left: 25%;
    bottom: 20%;
}

.quickReply {
    text-align: center;
    box-shadow: 0 9px 30px -7px rgb(165, 163, 163);
    font-family: Ubuntu, sans-serif;
    text-decoration: none;
    display: inline-block;
    text-transform: capitalize;
    outline: none;
    color: rgb(0, 0, 0);
    display: inline-block;
    cursor: pointer;
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 24px;
    background: linear-gradient(#ffffff 0%, rgb(165, 163, 163) 100%);
    padding: 7px 12px;
    border-width: 1px;
    border-style: solid;
    border-color: #3e7327;
    border-image: none;
    border-image: initial;
    margin: 5px;
    border-radius: 20px;
}

.quickReply:hover {
    background: #72b352 linear-gradient(to bottom, #72b352 5%, #77b55a 100%);
}

.inputFieldContainer {
    position: fixed;
    margin-left: 5%;
    margin-right: 5%;
    max-width: 800px;
    width: 70%;
    left: 20%;
    bottom: 10%;
}

.inputField {
    z-index: 9999;
    font-family: Ubuntu, sans-serif;
    font-size: large;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.9);
    box-shadow: 12px 12px 16px 0 rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    display: flex;
    justify-content: center;
    margin-right: 4rem;
    padding-left: 15px;
    width: 100%;
    height: 50px;
}

input {
    padding: 5px;
}

button {
    z-index: 9999;
    position: absolute;
    right: -10px;
    top: 7px;
    width: 70px;
    height: 45px;
    -moz-appearance: none;
    border: none;
    background: #ededed;
    border-radius: 3px;
    padding: 10px;
    margin-top: 2px;
    text-transform: capitalize;
    transition: all .2s;
}

button:hover {
    background-color: #67AB67;
    color: white;
}

button:active {
    background-color: #468546;
    color: white;
}

@media only screen and (max-width: 1024px) {
    .botOutputsContainer {
        position: fixed;
        top: 25%;
        left: 5%;
        right: 5%;
        padding: 0;
        margin: 0;
        width: 100%;
    }

    .chatbotOutput {
        max-width: 80%;
    }

    .inputFieldContainer {
        width: 80%;
        left: 0;
        margin-left: 5%;
        margin-right: 5%;
    }
}

@media only screen and (max-width: 640px) {

    .botOutputsContainer {
        position: fixed;
        top: 25%;
        left: 5%;
        right: 5%;
        padding: 0;
        margin: 0;
    }

    .chatbotOutput {
        max-width: 80%;
        font-size: larger;
    }

    .inputFieldContainer {
        width: 80%;
        top: 85%;
        margin-left: 5%;
        margin-right: 5%;
    }
}

@media only screen and (max-width: 568px) {

    .botOutputsContainer {
        position: fixed;
        top: 25%;
        left: 5%;
        right: 5%;
        padding: 0;
        margin: 0;
        width: 85%;
    }

    .chatbotOutput {
        max-width: 75%;
        font-size: large;
        max-height: 50px;
        padding: 12% 18% 20% 18%;
    }

    .inputFieldContainer {
        width: 80%;
        top: 85%;
        margin-left: 5%;
        margin-right: 5%;
    }
}

/* Smaller Smarthphones */
@media only screen and (max-width: 480px) {

    .chatbotOutput {
        min-width: 250px;
        margin-bottom: 3%;
        line-height: 20px;
        border-radius: 32px;
        padding: 14px 28px;
        display: inline-block;
        margin: 0 14px 14px 0;
    }

    .botOutputsContainer {
        position: fixed;
        top: 25%;
        left: 5%;
        padding: 0;
        margin: 0;
        width: 90%;

    }

    .inputFieldContainer {
        position: fixed;
        margin-left: 5%;
        margin-right: 5%;
        max-width: 800px;
        width: 70%;
        left: 7%;
        bottom: 2%;
    }

    .quickReplyContainer {
        position: absolute;
        left: 15%;
        bottom: 15%;
        display: table;
        margin: 0 auto;

    }
}

